<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center no-gutters">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            Brands
          </h3>
        </div>
        <div class="col text-right">
          <base-button type="info" @click="addBrand()" size="sm">Add Brand</base-button>
        </div>
      </div>
    </div>

    <div class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="tableData"
      >
        <template v-slot:columns>
          <th>Brand Name</th>
          <th>Action</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
              <div class="media-body">
                <span class="name mb-0 text-sm">{{ row.item.BrandName }}</span>
              </div>
          </th>

          <td class="text-right">
              <b-dropdown size="lg"  variant="link" toggle-class="text-decoration-none" no-caret>
                <template #button-content>
                  <i class="fas fa-ellipsis-v"></i>
                </template>
                <b-dropdown-item href="#"  >Edit</b-dropdown-item>
                <b-dropdown-item href="#" >Delete</b-dropdown-item>
              </b-dropdown>
          </td>
        </template>
      </base-table>
    </div>

    <div
      class="card-footer d-flex justify-content-end"
      :class="type === 'dark' ? 'bg-transparent' : ''"
    >
      <base-pagination :page-count="10" v-model="pagination.default"></base-pagination>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "brands-table",
  props: {
    type: {
      type: String,
    },
    title: String,
  },
  data() {
    return {
      tableData: [],
      perpage:8,
      currentpage:1,
       pagination: {
        default: 1
      }
    };
  },
  async mounted() {
      let brands = await axios.get(process.env.VUE_APP_SERVER_HOST+"/api/brand/");
      this.tableData = brands.data;
  },
  async created(){
      this.checkStore();
  },
  methods:{
    addBrand(){
        this.$router.push('/addbrand');
    },
    checkStore(){
        console.log(this.$store.state)
    }
  }
};
</script>
<style></style>
