<template>
  <div class="edit-product">
    <base-header type="gradient" class="pb-7 pt-7"> </base-header>
    <div class="container-fluid mt--7">
      <div class="row no-gutters justify-content-center">
         <div class="col-md-12 pl-3">
          <span>
            <i class="fas fa-chevron-left text-info" style="outline: 0;box-shadow: none!important;">
            </i>
            <router-link :to="{ name: 'Products'}" class="pl-2 text-info form-control-label" style="font-size: 16px;">Back</router-link>
          </span>
        </div>
      </div>

      <div class="row no-gutters justify-content-center mt-1 mb-6">
        <div class="col-12">


          <div class="card shadow bg-secondary mb-6">
            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row align-items-center">
                  <div class="col-12">
                    <h3 class="mb-0">Add Product</h3>
                  </div>
                </div>
              </div>
            </div>

            <div class="card-body">
              <form> 
                <h6 class="heading-small text-muted mb-4">Product information</h6>
                <div class="">
                   <div class="row">
                      <div class="col-lg-4">
                        <div class="form-group has-label">
                          <label class="form-control-label">
                             Brand
                          </label>
                         <select class="form-control form-control-alternative" v-model="BrandID">
                            <option disabled value="">Please select one</option>
                            <option
                              v-for="item in brand_lists"
                              :key="item.id"
                              :value="item.id"
                            >
                              {{ item.BrandName }}
                            </option>
                          </select>
                        </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Category
                            </label>
                            <select class="form-control form-control-alternative" v-model="selectedCategory">
                               <option disabled value="">Please select one</option>
                              <option
                                v-for="item in tag_hirarchy"
                                :key="item.category.id"
                                :value="item.category.id"
                              >
                                {{ item.category.tagName }}
                              </option>
                            </select>
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Subcategory
                            </label>
                            <select class="form-control form-control-alternative" :disabled="selectedCategory === ''" v-model="selectedSubCategory">
                              <option disabled value="">Please select one</option>
                              <option
                                v-for="item in subCatList[selectedCategory]"
                                :key="item.id"
                                :value="item.id"
                              >
                                {{ item.tagName }}
                              </option>
                            </select>
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Product Code
                            </label>
                            <input
                              placeholder="Product Code"
                              id="productCode"
                              v-model="ProductCode"
                              class="form-control form-control-alternative"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-8">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Product Name
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              id="ProductName"
                              v-model="ProductName"
                              placeholder="Product Name"
                              type="text"
                            />
                         </div>
                      </div>
                   </div>
                </div>
                <hr class="my-4">
                <h6 class="heading-small text-muted mb-4">Pricing Information</h6>
                <div class="">
                   <div class="row">
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Product Price
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              id="pricePerPiece"
                              v-model="ProductPrice"
                              placeholder="Price Per Piece"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Discounted Price
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              id="gstInput"
                              v-model="DiscountedPrice"
                              placeholder="GST"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Business Volume
                            </label>
                            <input
                              placeholder="Quantity / Set"
                              id="qtyBundle"
                              v-model="BusinessVolume"
                              class="form-control form-control-alternative"
                              type="text"
                            />
                         </div>
                      </div>
                   </div>
                </div>


                <hr class="my-4">
                <h6 class="heading-small text-muted mb-4">Product Description</h6>
                <div class="">
                  <div class="row">

                    <div class="col-lg-6">
                      <div class="form-group">
                        <div class="form-group has-label">
                           <label class="form-control-label"  for="productDescription">
                              Product Description
                           </label>
                          <textarea rows="4" type="text" placeholder="ProductDescription" id="productDescription" v-model="ProductDesc" class="form-control form-control-alternative"></textarea>
                        </div>
                      </div>
                    </div>
                    <div class="col-lg-6">
                      <div class="form-group">
                        <div class="form-group has-label">
                           <label class="form-control-label"  for="colorDescription">
                              Color Description
                           </label>
                          <textarea type="text"  rows="4" placeholder="Color Description" id="colorDescription" v-model="ColorDesc" class="form-control form-control-alternative" ></textarea>
                        </div>
                      </div>
                    </div>
                    <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Product Quantity
                            </label>
                            <input
                              placeholder="Quantity in gms/kg/ltr/etc"
                              v-model="quantity"
                              class="form-control form-control-alternative"
                              type="text"
                            />
                         </div>
                      </div>

                    <div class="col-lg-12">
                      <div class="form-group">
                        <div class="form-group has-label">
                          <label class="form-control-label">
                            Upload Images (upto 5)
                          </label>
                          <div class="col-lg-12 col-sm-12 col-md-12 image-box">
                            <div id="preview" class="row no-gutters justify-content-around p-2" style="">
                              <div v-if="url1" class="col-lg-2 col-md-2 col-sm-12 text-center"> 
                                <img width="70" height="70"  :src="url1" style="position:relative;" />

                                <div @click="removeImage(0)" class="text-center text-primary " style="cursor:pointer"><span class="btn btn-info btn-sm">Remove</span></div>
                              </div>
                              <div v-if="url2" class="col-lg-2 col-md-2 col-sm-12 text-center"> 
                                <img width="70" height="70"  :src="url2" />
                                <div @click="removeImage(1)" class="text-center text-primary" style="cursor:pointer"><span class="btn btn-info btn-sm">Remove</span></div>
                              </div>
                              <div v-if="url3" class="col-lg-2 col-md-2 col-sm-12 text-center"> 
                                <img width="70" height="70"  :src="url3" />
                                <div @click="removeImage(2)" class="text-center text-primary" style="cursor:pointer"><span class="btn btn-info btn-sm">Remove</span></div>
                              </div>
                              <div v-if="url4" class="col-lg-2 col-md-2 col-sm-12 text-center"> 
                                <img width="70" height="70"  :src="url4" />
                                <div @click="removeImage(3)" class="text-center text-primary" style="cursor:pointer"><span class="btn btn-info btn-sm">Remove</span></div>
                              </div>
                              <div v-if="url5" class="col-lg-2 col-md-2 col-sm-12 text-center">
                                <img width="70" height="70"  :src="url5" />
                                <div @click="removeImage(4)" class="text-center text-primary" style="cursor:pointer"><span class="btn btn-info btn-sm">Remove</span></div>
                              </div>

                            </div>
                            <input 
                              type="file"
                              name="picture"
                              id="new-file"
                              accept="image/*"
                              ref="file"
                              v-on:change="handleImageUpload()"
                              class="custom-file-input"
                              aria-label="picture"
                              style="background-color: pink"
                              multiple
                            />     
                            <div class="dropzone-text">Drop Files in this area or Click to upload</div>                        
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <hr class="my-4">
                <h6 class="heading-small text-muted mb-4">Image url (Upto 5)</h6>
                <div class="ImageUrls">
                  <div class="row" v-for="(img, index) in Imgurls" :key="img">
                    <div class="col-lg-6">
                      <div class="form-group">
                        <div class="form-group has-label">
                            <input type="text" :value="img" class="form-control" placeholder="Image url"/>
                        </div>
                      </div>
                    </div>
                    <div class="col-lg-4">
                      <div class="form-group">
                        <div class="form-group">
                            <button v-if="img" @click="removeImageUrl(index)" type="button" class="btn btn-secondary">Remove</button>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="row" v-if="Imgurls.length != 5">
                    <div class="col-lg-6">
                      <div class="form-group">
                        <div class="form-group has-label">
                            <input type="text" v-model="Imgurl" class="form-control" placeholder="Image url"/>
                        </div>
                      </div>
                    </div>
                    <div class="col-lg-4">
                      <div class="form-group">
                        <div class="form-group">
                            <button type="button" v-on:click="addImageUrl()" class="btn btn-info">+</button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                <hr class="my-4">
                <h6 class="heading-small text-muted mb-4">Product Status</h6>
                <div class="">
                  <div class="row">
                    <div class="col-lg-6">
                      <div class="form-group">
                        <div class="form-group has-label">
                           <label class="form-control-label">
                              Loyalty
                           </label>
                            <select class="form-control form-control-alternative" v-model="Loyalty">
                              <option value="true">Yes</option>
                              <option value="false">No</option>
                            </select>
                        </div>
                      </div>
                    </div>
                    <div class="col-lg-6">
                      <div class="form-group">
                        <div class="form-group has-label">
                           <label class="form-control-label">
                              Status
                           </label>
                            <select class="form-control form-control-alternative" v-model="Status">
                              <option value="true">Active</option>
                              <option value="false">InActive</option>
                            </select>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

              </form>
            </div>

            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row">
                  <div class="col-12 text-right">
                    <router-link type="primary" :to="{name:'Products'}" class="btn btn-secondary mr-2"
                      >Cancel</router-link>
                    <button
                      type="primary"
                      class="btn btn-info"
                      @click="addProduct()"
                      >Add Product</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
  input, select, textarea{
    color: rgba(122, 23, 117, 0.616);
    font-weight: bold;
  }
</style>
<script>
import axios from "axios";

export default({
    name: "Add Product",
    data(){
        return {
            brand_lists:[],
            BrandID:"",
            ProductCode:"",
            ProductName:"",
            ProductPrice:"0",
            DiscountedPrice:"0",
            BusinessVolume:"0",
            ProductDesc:"",
            ColorDesc:"",
            quantity:"",
            tag_hirarchy:[],
            subCatList:{},
            selectedCategory:'',
            selectedSubCategory:'',
            Status:true,
            Loyalty: false,
            ImageDatas: [],
            url1:'',
            url2:'',
            url3:'',
            url4:'',
            url5:'',
            Imgurls:[],
            Imgurl:"",

        }
    },
    async mounted() {
        let brands = await axios.get(process.env.VUE_APP_SERVER_HOST+`/api/brand/`);
        this.brand_lists = brands.data;
        this.listTagHirarchy();
    },
    methods: {
        async listTagHirarchy(){
           let listTags = await axios.get(process.env.VUE_APP_SERVER_HOST+`/api/tagHirarchy/`);
           this.tag_hirarchy = listTags.data;
           this.tag_hirarchy.forEach((data)=>{
             this.subCatList[data.category.id] = data.subcategory;
           })
       },
       async addProduct(){
           const {BrandID, ProductCode, ProductName, ProductPrice, selectedCategory, selectedSubCategory, DiscountedPrice, Availability, Status, BusinessVolume, ProductDesc,ColorDesc, quantity, Loyalty} = this;
           if(!BrandID || !ProductCode || !ProductName || !ProductPrice || !selectedCategory || !selectedSubCategory || !quantity){
               this.$toast.error(`Please fill all the required field`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
           }

           var data = JSON.stringify({
            "productCode": ProductCode,
            "productName": ProductName,
            "productPrice": ProductPrice,
            "brand": BrandID,
            "status": Status,
            "availability": Availability,
            "discount": DiscountedPrice,
            "ProductDescription": ProductDesc,
            "colorDesc": ColorDesc,
            "businessVolume": BusinessVolume,
            "quantity":quantity,
            "category":selectedCategory,
            "subcategory":selectedSubCategory,
            "loyalty":Loyalty
        });

        var config = {
        method: 'post',
        url: process.env.VUE_APP_SERVER_HOST+'/api/add/product/',
        headers: { 
            'Content-Type': 'application/json', 
            'Cookie': 'csrftoken=EUQ1DoHGzmryjVtkrFagOe7X9VbmYDz37CLuvW2AQHq6VWgfL8fj9LzjnZVo0yZI'
        },
        data : data
        };

       let response = await axios(config);
       console.log(response)
       if(response.status == 201){
          this.$toast.success(`Product added successfully!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
          });
          this.resetForm();
       }else{
         this.$toast.error(`Something went Wrong`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
       }
       },
       handleImageUpload(){
         let uploadedImage = this.$refs.file.files;
        uploadedImage.forEach((image) =>{
              if(this.url1 == ''){this.url1 = URL.createObjectURL(image);this.ImageDatas[0]=image;}
              else if(this.url2 == ''){this.url2 = URL.createObjectURL(image);this.ImageDatas[1]=image;}
              else if(this.url3 == ''){this.url3 = URL.createObjectURL(image);this.ImageDatas[2]=image;}
              else if(this.url4 == ''){this.url4 = URL.createObjectURL(image);this.ImageDatas[3]=image;}
              else if(this.url5 == ''){this.url5 = URL.createObjectURL(image);this.ImageDatas[4]=image;}
          })

       },
       removeImage(num){
         console.log("image", num)
       },
       removeImageUrl(image_num){
         this.Imgurls.splice(image_num,1);
       },
       addImageUrl(){
         if(!this.Imgurl)return;
         this.Imgurls.push(this.Imgurl);
         this.Imgurl = "";
       },
       resetForm(){
         this.BrandID = "";
         this.ProductCode = "";
         this.ProductName = "";
         this.ProductPrice = "";
         this.BrandID = "";
         this.Availability = "";
         this.DiscountedPrice = "";
         this.BusinessVolume = "";
         this.ProductDesc = "";
         this.ColorDesc = "";
         this.selectedCategory = "";
         this.selectedSubCategory = "";
       }
    }

})
</script>