<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            Order
          </h3>
        </div>
      </div>
    </div>

    <div class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="tableData"
      >
        <template v-slot:columns>
          <th>Order Number</th>
          <th>Order Date</th>
          <th>User</th>
          <th>DP Details</th>
          <th>Total Amount</th>
          <th>Status</th>
          <th>Action</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
              <div class="media-body">
                <span class="name mb-0 text-sm">
                    <a :href="'/#/order/' + row.item.id + '/product'" >{{row.item.orderNumber }}</a>
                </span>
              </div>
          </th>
           <td class="text-align-center">
            <div class="align-center">
                {{ row.item.created_at }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.user.email }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                <div class="font-weight-bold"><a :href="'/#/view/' + row.item.distributionpoint.id + '/distributionpoint'" >{{ row.item.distributionpoint.dpID }}</a></div>
                <div class="">{{ row.item.distributionpoint.dpName }}</div>
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                <div class="font-weight-bold">Rs. {{ calculateTotalamount(row.item.orderProducts) }}</div>
                <div v-if="calculateLength(row.item.orderProducts) === 1" class="font-italic">
                    ({{ calculateLength(row.item.orderProducts) }} Product)
                </div>
                <div v-else class="font-italic">
                    ({{ calculateLength(row.item.orderProducts) }} Products)
                </div>
            </div>
          </td>
          <td class="text-align-center">
            <div v-if="row.item.status" class="text-success">Active</div>
            <div v-else class="text-danger">InActive</div>
          </td>
          <td class="text-right">
              <b-dropdown size="lg"  variant="link" toggle-class="text-decoration-none" no-caret>
                <template #button-content>
                  <i class="fas fa-ellipsis-v"></i>
                </template>
                <b-dropdown-item href="#"  >Edit</b-dropdown-item>
                <b-dropdown-item href="#" >Delete</b-dropdown-item>
              </b-dropdown>
          </td>
        </template>
      </base-table>
    </div>

    <div
      class="card-footer d-flex justify-content-end"
      :class="type === 'dark' ? 'bg-transparent' : ''"
    >
      <base-pagination :page-count="10" v-model="pagination.default"></base-pagination>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
    name: 'orderTable',
    props: {
        type: {
            type: String,
        },
        title: String,
    },
    data() {
        return {
            tableData: [],
            orderProducts: {},
            totalPriceData: {},
            perPage:8,
            currentpage:1,
            pagination: {
                default: 1
            }
        }
    },
    async mounted(){
      let orders = await axios.get(process.env.VUE_APP_SERVER_HOST+"/api/order/");
      this.tableData = orders.data;
  },
  methods: {
      calculateTotalamount(dataArr) {
          if(dataArr.length === 1){
              return ((parseFloat(dataArr[0].PricePerPiece)) * dataArr[0].productQty).toFixed(2)
          }else{
              let res = dataArr.reduce((acc, item)=>{
                return (parseFloat(acc.PricePerPiece) * acc.productQty) + (parseFloat(item.PricePerPiece) * item.productQty) 
            })
            return (res).toFixed(2);
          }  
      },
      calculateLength(dataArr){
          return dataArr.length;
      }
  }
}
</script>